// 排序
function sortHide() {
  $('.sort-lst').hide();
  $('.sort-mask').hide();
  $('.sort-box .text').removeClass('open');
}

;
$('.sort-box').on('click', '.text', function () {
  $(this).addClass('open');
  $('.sort-lst').show();
  $('.sort-mask').show();
});
$('.sort-lst').on('click', function () {
  sortHide();
});
$('.sort-mask').on('click', function () {
  sortHide();
});
$('.sort-box .sort-lst').on('click', 'li', function () {
  $(this).parents('.sort-lst').find('li').removeClass('active');
  $(this).addClass('active');
  var txt = $(this).text();
  $('.sort-box .text').text(txt);
  sortHide();
}); // // 取消收藏
